* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  min-height: 100vh;
  background: url("js.858368c6.jpg") center / cover no-repeat;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  list-style: none;
}

.custom-btn {
  color: #fff;
  width: 150px;
  height: 40px;
  cursor: pointer;
  background: none;
  margin: 10px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-weight: 500;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.btn-1 {
  color: #ff9aff;
  z-index: 2;
  background: #000;
  border: 1px solid #ff9aff;
  transition: all .3s;
  overflow: hidden;
  box-shadow: 0 0 5px #ff9aff, inset 0 0 5px #ff9aff;
}

.btn-1:after {
  content: " ";
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #ff9aff;
  transition: all .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: scale(0);
}

.btn-1:hover {
  color: #fff;
}

.btn-1:hover:after {
  transform: scale(1);
}

